/*---------------------------Upper Header-------------*/
.nav-link{
  font-size: 14px;
  font-weight: bold;
  text-align: center;
  color: #e5e5e5;
  text-transform: uppercase;
  margin-right: 5px;
  padding: 10px 15px!important;
  -webkit-transition: ease .2s;
  transition: ease .2s;
  cursor: pointer;
}
.nav-link:hover, .nav-link:focus{
  background-color: #cc2027;
  color: #fff;
}
.navbar{
  padding: 0;
  background-color: #222222;
  min-height: 41px;
}
