  }
}
/*---------------------------Clock-------------*/
.time-clock{
  position: absolute;
  top: 10px;
  left: 0;
  display: flex;
  align-items: center;
}
.current-time{
	font-size: 14px;
  font-weight: 600;
  color: #e5e5e5;
  margin: 0 5px;
}
.fa-clock{
	color: #e5e5e5;
	margin-left: 20px;
	font-size: 18px;
}
.time-clock span{
	color: #e5e5e5;
	font-size: 14px;
	font-weight: 600;
}
@media only screen and (max-width: 500px){
  .current-time, .time-clock span{
    font-size: 12px;
  }
  .fa-clock{
    font-size: 14px;
  }
}
/*---------------------------Lang bar-------------*/
.lang-bar{
  position: absolute;
  right: 0px;
  top: 0px;
  z-index: 99;
}
.lang-bar span{
	background-color: #7b0005;
  color: #fff;
  margin-right: 3px;
  cursor: pointer;
  border-radius: 0px;
  -webkit-transition: ease .2s;
  transition: ease .2s;
  display: inline-block;
  height: 41px;
  padding: 9px 10px;
}
.lang-bar span:last-of-type{
  margin: 0;
}
.lang-bar span:hover{
	background-color: #cc2027;
}
@media only screen and (max-width: 991px){
  .lang-bar span{
    font-size: 14px;
    padding: 11px;
  }
}
@media only screen and (max-width: 500px){
  .lang-bar span {
    padding: 7px 12px;
    height: 35px;
  }
}
